body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}


.wrapper{
  width: 98vw;
  margin: 10px auto;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-flow: wrap;
}
.card{
    width: 250px;
    height: 250px;
    border: 1px solid #4BE297;
    border-radius: 25px;
    box-sizing: border-box;
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #8FF21F;
    transition: transform 500ms ease;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.card .card-title{
    margin: 5px 0;
    text-align: center;
    font-weight: bold;
}
.card .card-title.sub{
    font-weight: normal;
}

.card .profile{
    width: 150px;
    height: 150px;
    border-radius: 50%;
}
.card:hover{
    transform: scale(1.05);
    cursor: pointer;
}
.search{
    display: block;
    margin: 10px auto;
    width: 25%;
    padding: 10px;
}
.main {
    margin: 0;
    padding: 10px;
    width: 100vw;
    height: 100%;
    min-height: 100vh;
    box-sizing: border-box;
    background: linear-gradient(
            to left,
            #56CBD6 0%,
            #649AFA 100%
    );
}

h1{
    color: white;
}
